.projects-grid {
  display: grid;
  padding-bottom: 2vh;
  letter-spacing: 0.01em;
  align-items: center;
  color:#1d1d1d;
  align-self: center;
  align-items: start;
  align-content: start;
  grid-auto-columns: 1fr;
  grid-column-gap: 2vw;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.featured-project {
  position: relative;
}

.project-gallery-img {
  width: 100%;
  height: 35vw;
  object-fit: cover;
  opacity: 0.8;
  transition: opacity .8s ease-in-out, transform .4s ease-in-out;
}

.project-gallery-img:hover {
  transform: translateY(-5px); 
  opacity: 1;
}

.centered-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -300%);
  z-index: 1;
}

.under-construction {
  opacity: 0;
  font-size: 0.8rem;
  font-style: italic;
  color: #f3f3f3;
  /* transition: opacity 0.3s ease; */
  z-index: 2;
}
.featured-project:hover .under-construction {
  opacity: 1;
}

.Text-Stack {
  padding-top: 0.5rem;
  font-size: 0.6rem;
}

.Text-Title {
  padding-top: 0.4rem;
  font-size: 1.2rem;
}

@media only screen and (max-width: 768px) 
and (orientation: portrait) {
  /* For tablets & mobile phones: */
  .projects-grid {
    grid-template-columns: 1fr 1fr;
  }

  .featured-project {
    padding-bottom: 3vh;
  }

  .project-gallery-img {
    height: 70vw;
  }

}