.home {
  padding-top: 20vh;
  padding-bottom: 15vh;
}

.intro-text {
  width: 75%;
  white-space: pre-wrap;
  font-family: 'SK-Modernist';
  font-size: 8vw;
  line-height: 90%;
  margin-left: -0.07em;
  margin-bottom: 0;
  padding-bottom: 2.5vh;
}

.intro-animate-1 {
  display: inline-block !important;
  background-color: transparent !important;
  animation: intro-animation 2s !important;
}

.intro-animate-2 {
  display: inline-block !important;
  background-color: transparent !important;
  animation: intro-animation 1.5s !important;
}

.intro-animate-3 {
  font-size: 8.1vw;
  display: inline-block !important;
  background-color: transparent !important;
  animation: intro-animation 1s !important;
}

@keyframes intro-animation {
  from {
    bottom: -5%;
    opacity: 0%;
  }
  to {
    bottom: 0%;
    opacity: 100%;
  }
}

@media only screen and (max-width: 768px) 
and (orientation: portrait) {
  /* For tablets & mobile phones: */

  .home {
    padding-bottom: 7.5vh;
  }

  .intro-text {
    padding-bottom: 0;
  }
}