h1 {
  font-family: 'SK-Modernist';
  font-size: 4rem;
  letter-spacing: -0.02em;
}

hr.solid {
  border-top: 0.01px solid #5c5c5c;
  border-bottom: 0;
  border-left: 0;
  border-right: 0;
  margin-bottom: 5vh;
}

.main-text {
  font-size: 0.85rem;
  line-height: 1.2rem;
  letter-spacing: 0.002rem;
}

.container {
  margin-left: 2vw;
  margin-right: 2vw;
}

.container-route {
  padding-top: 20vh;
  padding-bottom: 7vh;
  /* text-transform: uppercase; */
}

.container-app {
  padding-top: 5vh;
  padding-bottom: 7vh;
  /* background-color: rgb(190, 190, 190); */
}

.Fade-in {
  animation: fade-in 1s !important;
}

.Fade-in-b {
  animation: fade-in 1.5s !important;
}

@keyframes fade-in {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}

.fade-in-section {
  opacity: 0;
  transform: translateY(2.5vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

.fade-in-section-b {
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.6s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section-b.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}

/* for leaflet */

.leaflet-container {
  width: 100%;
  height: 100vh;
  background: transparent !important;
  outline: 0;
  z-index: 1;
}

.leaflet-tile {
  filter: grayscale(100%) brightness(50%) contrast(500%) invert(100%) sepia(0%) saturate(5756%) hue-rotate(43deg) brightness(119%) contrast(91%)!important;
}

.leaflet-popup-content-wrapper, .leaflet-popup-tip {
  background: none !important;
  color: none !important;
  box-shadow: none !important;
}

.leaflet-container .leaflet-control-attribution {
	background: transparent !important;
	}

.popup {
  font-size: 50px;
} 

