.project-grid {
  display: grid;
  padding-top: 2vh;
  padding-bottom: 2vh;
  letter-spacing: 0.01em;
  align-items: center;
  color:#1d1d1d;
  align-self: center;
  align-items: start;
  align-content: start;
  grid-auto-columns: 1fr;
  grid-column-gap: 2vw;
  grid-template-columns: 1fr 1fr;
}

.project-title{
  padding-bottom: 7vh;
}

.project-img {
  width: 100%;
  height: 25vw;
  object-fit: cover;
  opacity: 0.8;
  transition: opacity .8s ease-in-out, transform .4s ease-in-out;
}

.project-img:hover {
opacity: 1;
}

.project-content {
  display: grid;
  padding-top: 2vh;
  padding-bottom: 2vh;
  letter-spacing: 0.01em;
  align-items: center;
  color:#1d1d1d;
  align-self: center;
  align-items: start;
  align-content: start;
  grid-auto-columns: 1fr;
  grid-column-gap: 2vw;
  grid-template-columns: 1fr 3fr 0.1fr;
}

.project-video {
  padding-bottom: 9vh;
}

.text-heading {
  padding-top: 1rem;
  font-style: italic;
  font-size: 0.85rem;
}

.text-body {
  padding-top: 0.5rem;
  padding-bottom: 2.5rem;
  width: 90%;
  font-size: 0.85rem;
}

@media only screen and (max-width: 1024px) 
and (orientation: landscape) {
  /* For tablets & mobile phones: */
  .project-grid {
    grid-template-columns: 0.1fr 1fr;
  }

  .project-content {
    grid-template-columns: 0.1fr 3fr 0.1fr;
  }

  .project-video {
    padding-bottom: 1.5vh;
  }

}

@media only screen and (max-width: 768px) 
and (orientation: portrait) {
  /* For tablets & mobile phones: */
  .project-grid {
    grid-template-columns: 0.1fr 1fr;
  }

  .project-content {
    grid-template-columns: 0.1fr 3fr 0.1fr;
  }

  .project-video {
    padding-bottom: 1.5vh;
  }

}