.footer {
  padding-top: 7vh;
}

.footer-flex {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.footer-item {
  font-weight: bold;
  list-style: none;
  display: inline-block;
  padding-left: 0.8rem;
  font-size: 1.5rem;
  opacity: 0.7;
  transition: opacity 0.8s;
}

.footer-item > .icons {
  color: #1f1f1f;
}

.footer-item:hover {
  opacity: 1;
}

.to-top {
  opacity: 1;
  transition: visibility 0s linear 0s, opacity 500ms;
  position: fixed;
  bottom: 5vh;
  text-align: left;
  font-size: 0.6rem;
  font-style: italic;
  cursor: pointer;
}

.to-top-hidden {
  opacity: 0;
  transition: visibility 0s linear 500ms, opacity 500ms;
  position: fixed;
  bottom: 5vh;
  text-align: left;
  font-size: 0.6rem;
  font-style: italic;
  cursor: pointer;
}


@media only screen and (max-width: 768px) 
and (orientation: portrait) {
  /* For tablets & mobile phones: */
  .footer {
    padding-top: 10vh;
  }
  
}

